import React, { useEffect, useRef, useState, createContext, useContext, useMemo } from "react";
import { register, getUniqueComponentsToRegister } from "../commons/ComponentRegistry";
import wrapper from "../commons/WebComponentWrapper";
import { debounce, isEqual } from 'lodash';
import { compressToBase64, decompressFromBase64 } from "lz-string";
import { klona } from "klona";

const ContentRenderer = (props, ref) => {

  const [contents, setContents] = useState(props.widgetid ? (props.overrides?.contents || []) : (props.overrides?.contents || props.clan || []));
  const [context, setContext] = useState(props.widgetid ? (props.overrides?.context || {}) : (props.overrides?.context || props.context || {}));
  const [functions, setFunctions] = useState(props.widgetid ? (props.overrides?.functions || {}) : (props.overrides?.functions || props.functions || {}));
  const [designtime, setDesigntime] = useState(props.designtime);
  const [widgetId, setWidgetId] = useState(props.widgetid || "");
  const [theme, setTheme] = useState(props.theme || "g90");
  const [componentsRegistered, setComponentsRegistered] = useState(false);
  const [uniqueComponents, setUniqueComponents] = useState(getUniqueComponentsToRegister(props.widgetid ? (props.overrides?.contents || []) : (props.overrides?.contents || props.clan || [])));
  const [componentPropTypes, setComponentPropTypes] = useState({});
  const innerRef = useRef(null);
  const [prevContents, setPrevContents] = useState({});
  const [processedNodes, setProcessedNodes] = useState([]);

  const updateFunctions = (functionData) => {
    if (functionData && Object.keys(functionData).length > 0) {
      window.__SRS__ = window.__SRS__ || {};
      window.__SRS__.functions = window.__SRS__.functions || {};
      let functionNames = Object.keys(functionData);
      functionNames.map((functionName) => {
        let functionValue = functionData[functionName];
        if (functionValue) {
          if (typeof functionValue == "string") {
            window.__SRS__.functions[functionName] = new Function("try { return " + functionValue + " }catch(err) {console.error(err);}")();
          } else {
            window.__SRS__.functions[functionName] = functionValue;
          }
        }
      });
    }

    setFunctions(prevContext => functionData);
  }

  useEffect(() => {
    setContents(props.overrides?.contents || props.clan || []);
    // setPrevContents(props.overrides?.contents || props.clan || []);
    setContext(props.overrides?.context || props.context || {});
    updateFunctions(functions);

    if (props.overrides?.hasOwnProperty("designtime")) {
      setDesigntime(props.overrides.designtime);
    } else if (props.designtime) {
      setDesigntime(props.designtime);
    }
  }, []);

  useEffect(() => {
    setPrevContents([...processedNodes]);
    let result = getNodesToRender(klona(contents), context, contents, theme, prevContents, designtime, widgetId);
    setProcessedNodes(result);
  }, [contents, context]);

  useEffect(() => {
    setContents(currentContents => {
      let updatedContents = props.overrides?.contents || props.clan;
      return [...updatedContents];
    });

    setContext(prevContext => {
      let updatedContext = props.overrides?.context || props.context;
      return { ...updatedContext };
    });
    updateFunctions(props.overrides?.functions || props.functions || {});
    setTheme(prevTheme => props.theme);

    if (props.overrides?.hasOwnProperty("designtime")) {
      setDesigntime(props.overrides.designtime);
    } else if (props.designtime) {
      setDesigntime(props.designtime);
    }

    let updatedUniqueComponents = getUniqueComponentsToRegister(props.overrides?.contents || props.clan || []);
    if (!componentsRegistered || isChanged(updatedUniqueComponents, uniqueComponents)) {
      setUniqueComponents(prevVal => updatedUniqueComponents);
      setComponentsRegistered(prevVal => false);
      register(updatedUniqueComponents, props.theme)
        .then((componentPropTypeData) => {
          setComponentPropTypes(prevVal => componentPropTypeData);
          setComponentsRegistered(prevVal => true);
        });
    }
  }, [props.overrides, props.clan, props.context, props.theme]);

  let getRenderedContents = () => {
    let result = null;
    if (processedNodes && processedNodes.length > 0) {
      if (componentsRegistered) {
        let styles = { backgroundColor: "var(--cds-background)", minHeight: "100vh", maxHeight: "100vh", overflow: "auto" };
        let propStyles = props.styles || {};
        styles = { ...styles, ...propStyles };
        let carbonStylesUrl = "./carbon-components.css";
        if (window.__SRS__?.mode == "sandbox") {
          carbonStylesUrl = "https://know.srste.in/carbon-components.css";
        }
        result = (<div ref={innerRef} data-carbon-theme={props.theme} style={styles}>
          <style className="basetheme">
            {`@import "${carbonStylesUrl}";`}  {/* DONT REMOVE THIS STYLE AS THIS IS USED FOR THEME */}
          </style>
          {renderNodes(klona(processedNodes), context, processedNodes, theme, designtime, widgetId)}
        </div>)
      } else {
        result = <div> loading... </div>;
      }
    } else {
      result = <div style={{ color: "var(--cds-text-secondary)", display: "flex", flexDirection: "column", alignItems: "center", fontSize: "x-large" }}> No contents to show!</div>;
    }
    return result;
  };

  let reRenderContents = useMemo(getRenderedContents, [processedNodes, componentsRegistered]);
  return reRenderContents;
};

const isChanged = function (array1, array2) {
  let isChanged = false;

  array1.forEach((array1Item) => {
    if (array2.indexOf(array1Item) == -1) {
      isChanged = true;
    }
  });
  return isChanged;
}

const getNodeProps = function (node, propKeys, nodeContext) {
  let gridItemProps = {};

  if (propKeys && propKeys.length > 0) {
    propKeys.forEach((propKey) => {
      if (node.props[propKey]) {
        gridItemProps[propKey] = node.props[propKey];
      }
    })
  }

  if (node.overrides) {
    let overridePropKeys = Object.keys(node.overrides);

    overridePropKeys.map((nodePropKey) => {
      let nodeVal = node.overrides[nodePropKey];
      if (nodeVal.startsWith("[[") && nodeVal.endsWith("]]")) {
        // remove the expression delimiters from the value
        let expression = nodeVal.slice(2, -2);
        let dynamicFunction = null;
        try {
          dynamicFunction = new Function("api", `with(api) { try { console.log("In dynamicFunction", "${expression}"); return ${expression}; }catch(err) { console.error(err) } }`);
          gridItemProps[nodePropKey] = dynamicFunction(nodeContext);
        } catch (err) { console.error("expression is ", expression, err); }
      }
    });
  }

  return gridItemProps;
}

const findElement = (key, nestedContents) => {
  for (let element of nestedContents) {
    if (element.elementId === key) {
      return element;
    } else if (element.children) {
      const found = findElement(key, element.children);
      if (found) return found;
    }
  }
  return null;
}

const findParentElement = (elementId, nestedContents) => {
  const stack = [...nestedContents];

  while (stack.length > 0) {
    const element = stack.pop();

    if (element.elementId === elementId) {
      return null; // The provided elementId is already the top-level element, so no parent exists
    }

    if (element.children) {
      for (let i = 0; i < element.children.length; i++) {
        const childElement = element.children[i];

        if (childElement.elementId === elementId) {
          return element; // Return the current element as the parent if a match is found
        }

        if (childElement.children) {
          stack.push(childElement); // Add the child element to the stack for further iteration
        }
      }
    }
  }

  return null; // Return null if no parent element is found
};

const getNodesToRender = function (nodes, context, clan, theme, currentRef, designtime, widgetId) {
  let result = [];
  // Compare previous and current node values 
  let availableFunctions = {};
  if (widgetId) {
    if (window.__SRS__?.functions && Object.keys(window.__SRS__.functions)) {
      Object.keys(window.__SRS__.functions).map((functionName) => {
        availableFunctions[functionName] = window.__SRS__.functions[functionName];
      })
    }
  } else {
    if (window.__SRS__?.functions && Object.keys(window.__SRS__.functions)) {
      Object.keys(window.__SRS__.functions).map((functionName) => {
        availableFunctions[functionName] = window.__SRS__.functions[functionName];
      })
    }
  }

  const nodeContext = {
    context,
    clan,
    dispatchEvent: props?.dispatchEvent || (() => { }),
    helper: {
      //add helper methods
      ...availableFunctions,
      findParentElement,
      findElement,
      compress: compressToBase64,
      decompress: decompressFromBase64,
      clone: klona,
      debounce: debounce
    }
  }

  nodes && nodes.forEach(node => {
    let NodeTag = node.tag;
    let nodeProps = {};

    if (node.props) {
      let nodePropKeys = Object.keys(node.props);
      nodePropKeys = nodePropKeys.filter((nodePropKey) => nodePropKey != "slotname");
      nodePropKeys.map((nodePropKey) => {
        nodeProps[nodePropKey] = node.props[nodePropKey];
      });
      if (node?.props?.slotname) {
        nodeProps.slot = node.props.slotname;
      }
    }

    if (node.overrides) {
      let overridePropKeys = Object.keys(node.overrides);
      overridePropKeys.map((nodePropKey) => {
        let nodeVal = node.overrides[nodePropKey];
        if (nodeVal.startsWith("[[") && nodeVal.endsWith("]]")) {
          // remove the expression delimiters from the value
          let dynamicFunction = null;
          let expression = nodeVal.slice(2, -2);
          try {
            dynamicFunction = new Function("api", `with(api) { try { return ${expression}; }catch(err) { console.error(err); } }`);
            nodeProps[nodePropKey] = dynamicFunction(nodeContext);
          } catch (err) { console.error("expression is ", expression, err); }
        }
      });
    }

    let nodeChildren = node.children;
    if (NodeTag == "mui-grid" && nodeProps.hasOwnProperty("container") && nodeChildren && nodeChildren.length > 0) {
      let nextLevelNodeChildren = [];
      let gridItemData = [];
      nodeChildren.forEach((nodeChild, indx) => {
        if (nodeChild && nodeChild.children) {
          let gridItemProps = getNodeProps(nodeChild, ["sx", "xs", "sm", "md", "lg", "xl", "spacing", "direction",
            "justify", "alignItems", "alignContent", "wrap", "component", "xsAuto", "smAuto", "mdAuto", "lgAuto", "xlAuto", "zeroMinWidth"], nodeContext);
          let gridItemSlotName = "grid-item-" + (nodeChild.props.elementId || nodeChild.props.name || indx);
          gridItemData.push({ slotname: gridItemSlotName, gridItemProps });
          nodeChild.children.map((nodeGrandChild, indx) => {
            nodeGrandChild.props = nodeGrandChild.props || {};
            nodeGrandChild.props.slotname = gridItemSlotName;
            nextLevelNodeChildren.push(nodeGrandChild);
          });
        }
      });
      nodeChildren = nextLevelNodeChildren;
      nodeProps["griditemdata"] = gridItemData;
    } else if (NodeTag == "mui-tabs" && nodeChildren && nodeChildren.length > 0) {
      let nextLevelNodeChildren = [];
      let tabItems = [];
      nodeChildren.forEach((nodeChild, indx) => {
        if (nodeChild && nodeChild.children) {
          let tabItemProps = getNodeProps(nodeChild, ["label", "value", "disabled", "icon", "wrapped"], nodeContext);
          let tabSlotName = "tab-item-" + (nodeChild.props.elementId || nodeChild.props.name || indx);
          tabItems.push({ slotname: tabSlotName, ...tabItemProps });
          nodeChild.children.map((nodeGrandChild, indx) => {
            nodeGrandChild.props = nodeGrandChild.props || {};
            nodeGrandChild.props.slotname = tabSlotName;
            nextLevelNodeChildren.push(nodeGrandChild);
          });
        }
      });
      nodeChildren = nextLevelNodeChildren;
      nodeProps["tabitems"] = tabItems;
    } else if (NodeTag == "srs-repeater" && nodeChildren && nodeChildren.length > 0) {
      if (designtime + "" != "true") {
        let items = [];
        if (typeof nodeProps.items == "string") {
          try {
            items = JSON.parse(nodeProps.items);
          } catch (err) {
            // console.error("Unable to parse the items of the repeater. Please correct it and try again.")
          }
        } else {
          items = nodeProps.items || [];
        }

        if (items && items.length > 0) {
          let currentNodeChildren = klona(node.children || []);
          nodeChildren = [];
          items.forEach((childItemContext, indx) => {
            nodeChildren.push({
              elementId: node.elementId + "-" + indx,
              name: node.name + "-" + indx,
              tag: "srs-contentrenderer",
              props: {
                overrides: {
                  contents: currentNodeChildren,
                  context: { ...context, ...childItemContext },
                  designtime: designtime
                },
                styles: { minHeight: "100%", maxHeight: "100%", overflow: "auto" }
              }
            })
          })
          node.tag = "mui-box";
          node.children = nodeChildren;
        } else {
          node.tag = "mui-box";
          nodeChildren = [];
          node.children = nodeChildren;
        }
      }
    } else if (NodeTag == "srs-widget") {
      let widgetFunctions = node.props?.widgetfunctions || {};
      widgetFunctions = typeof widgetFunctions == "string" ? JSON.parse(widgetFunctions) : widgetFunctions;
      if (widgetFunctions && !Array.isArray(widgetFunctions)) {
        if (widgetFunctions && Object.keys(widgetFunctions).length > 0) {
          window.__SRS__ = window.__SRS__ || {};
          window.__SRS__.functions = window.__SRS__.functions || {};
          let functionNames = Object.keys(widgetFunctions);
          nodeProps.widgetfunctions = {};
          functionNames.map((functionName) => {
            nodeProps.widgetfunctions[functionName] = "";
            let functionValue = widgetFunctions[functionName];
              if (functionValue) {
                if (typeof functionValue == "string") {
                  window.__SRS__.functions[functionName] = new Function("try { return " + functionValue + " }catch(err) { console.error(err); }")();
                } else {
                  window.__SRS__.functions[functionName] = functionValue;
                }
              }
          });
        }
      }
    } else if (NodeTag == "mui-modal") {
      if (nodeProps.open == false) {
        nodeProps.visible = false;
      } else {
        nodeProps.visible = true;
      }
    }

    if (!nodeProps.hasOwnProperty("visible") || nodeProps.visible + "" == "true") {
      let childNodes = getNodesToRender(nodeChildren, context, clan, theme, currentRef, designtime, widgetId);
      node.props = nodeProps;

      result.push({ ...node, children: childNodes });
    }
  });

  return result;
}

const renderNodes = function (nodes, context, clan, theme, designtime, widgetId) {
  let result = [];

  nodes && nodes.forEach(node => {
    let childNodes = renderNodes(node.children, context, clan, theme, designtime, widgetId);
    let NodeTag = node.tag;
    result.push(<NodeTag
      key={node.elementId || node.id || node.name}
      elementid={node.elementId || node.id || node.name}
      style={node.props?.hoststyles || {}}
      ref={el => {
        if (!el) return;
        el.context = context;
        el.clan = clan;
        el.theme = theme;
        el.elementid = node.elementId || node.id || node.name;
        el.widgetid = widgetId;
        el.designtime = designtime;
        Object.keys(node.props).forEach(nodePropKey => {
          try {
            if (typeof node.props[nodePropKey] == "object") {
              if (Array.isArray(node.props[nodePropKey])) {
                el[nodePropKey] = [...node.props[nodePropKey]];
              } else {
                el[nodePropKey] = { ...node.props[nodePropKey] };
              }
            } else {
              el[nodePropKey] = node.props[nodePropKey];
            }

          } catch (err) {
            // console.error(err);
          }
        });
      }}>
      {childNodes}
    </NodeTag>);
  });

  return result;
}

const dispatchedEvents = [];

const styles = `
    
`
const props = [{
  name: "overrides",
  type: "object",
  defaultValue: {}
}, {
  name: "widgetid",
  type: "string",
  defaultValue: ""
}, {
  name: "styles",
  type: "object",
  defaultValue: {}
}]

const slots = [];

export default ContentRenderer;
const WrappedComponent = wrapper(React.forwardRef(ContentRenderer), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
